.container {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	flex-grow: 1;
	box-shadow: 0 1px 1px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.26);
	padding: 10px;
	transition: all 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.container:hover {
	box-shadow: 0 10px 20px rgba(0, 0, 0, 0.12), 0 10px 10px rgba(0, 0, 0, 0.26);
}

.card-name {
	font-size: 15px;
	margin: 0;
	margin-bottom: -15px;
}

.card-value {
	font-size: 50px;
	font-weight: 800;
	margin: 0;
}
