.typing-challenge {
	display: flex;
	flex-direction: column;
	flex-grow: 1;
	align-items: center;
	justify-content: center;
}
.timer-container {
	margin: 16px;
}

.timer {
	font-size: 35px;
	font-weight: 600;
	margin: 0;
	text-align: center;
}

.timer-info {
	margin: 0;
	margin-top: -5px;
	color: #dd5044;
	font-size: 20px;
	text-align: center;
}

.textarea-container {
	margin: 16px;
	display: flex;
	flex-grow: 1;
	width: 80%;
}

.textarea-left,
.textarea-right {
	display: flex;
	width: 50%;
	flex-grow: 1;
}

.textarea {
	text-align: left;
	flex-grow: 1;
	height: 400px;
	width: 100%;
	padding: 10px;
	line-height: 18px;
	flex-wrap: wrap;
	overflow: scroll;
	transition: all 0.2s;
	box-shadow: 0 1px 1px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.26);
}

.textarea::-webkit-scrollbar {
	display: none;
}

.textarea:hover {
	box-shadow: 0 10px 10px rgba(0, 0, 0, 0.12), 0 10px 10px rgba(0, 0, 0, 0.26);
}

.test-paragraph {
	font-size: 12px;
	background: #e9e7e4;
	padding: 12px;
}
